body {
  background-color: #fff;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.card {
  background-color: #e0ecfa;
  border-radius: 12px;
  margin-top: 15px;
  margin-left: 10px;
  margin-right: 10px;
  padding-top: 15px;
  padding-bottom: 47vh;
  position: relative;
  box-shadow: 2px 4px 25px #00000080;
}

h1 {
  font-size: 25px;
}

label.logo {
  color: #000;
  float: left;
  padding-top: 5px;
  padding-left: 1%;
  font-size: 35px;
}

.topnav {
  background-color: #8bbbf3;
  align-items: center;
  padding-top: 15px;
  padding-bottom: 65px;
}

.topnav ul {
  float: right;
  z-index: 999;
  text-decoration: none;
  list-style: none;
}

.topnav ul li {
  margin-right: 25px;
  display: inline-block;
}

.topnav ul li a {
  color: #2d2d2d;
  background-color: #fff;
  border: 5px solid #fff;
  border-width: 5px 25px;
  border-radius: 25px;
  padding: 10px;
