}

.topnav ul li a.active {
  font-weight: bold;
  text-decoration: underline;
}

.topnav .icon {
  display: none;
}

.checkbutton {
  float: right;
  color: #fff;
  cursor: pointer;
  margin-right: 25px;
  font-size: 25px;
  display: none;
}

#check {
  display: none;
}

@media (max-width: 1249px) {
  label.logo {
    margin-top: -10px;
    margin-left: 15px;
    font-size: 30px;
  }

  .topnav {
    justify-content: space-between;
    align-items: center;
    padding-top: 15px;
    padding-bottom: 45px;
  }

  .topnav ul {
    margin-top: -20px;
  }

  .topnav ul li a {
    border-style: none;
    border-radius: 0%;
    padding: 0;
    font-size: 15px;
  }
}

@media (max-width: 1249px) {
  .checkbutton {
    display: block;
  }

  .topnav ul {
    width: 100%;
    height: 100vh;
    text-align: left;
    background-color: #fff;
    transition: all .5s;
    position: fixed;
    top: 80px;
    left: -120%;
  }

  .topnav ul li {
    margin-top: 25px;
    display: block;
  }

  .topnav ul li a {
    font-size: 20px;
  }

  #check:checked ~ ul {
    left: 0;
  }
}

h1 {
